export const sizeStyles = (variant, fourCols = true) => {
    const styles = [
        css`
        /* Tablet */
        @media screen and ${unsafeCSS(TABLET_UP)} {
            :host([variant='${unsafeCSS(variant)}'][size='wide']),
            :host([variant='${unsafeCSS(variant)}'][size='super-wide']) {
                grid-column: span 2;
                width: 100%;
            }
        }

        /* Laptop */
        @media screen and ${unsafeCSS(DESKTOP_UP)} {
            :host([variant='${unsafeCSS(variant)}'][size='super-wide']) {
                grid-column: span 3;
            }
        `,
    ];

    if (fourCols) {
        styles.push(css`
            /* Large desktop */
            @media screen and ${unsafeCSS(LARGE_DESKTOP)} {
                :host([variant='${unsafeCSS(variant)}'][size='super-wide']) {
                    grid-column: span 4;
                }
            }
        `);
    }
    return styles;
};
